/* Clear Flex*/
@each $size in $base-size-name {
	@include media-breakpoint-down($size) {
		/* Clear Flex */
		.clearflex-#{$size} {
			display: block!important;
		}
		
		/* Float */
		.float-#{$size}-right-i {
			float: right!important;
		}

		/* Text Align */
		.text-center-#{$size}-i {
			text-align: center!important;
		}

		/* Display */
		.d-#{$size}-none-i {
			display: none!important;
		}

		/* Order */
		@for $i from 1 through 11 {
			.order-#{$size}-#{$i}-i {
				-ms-flex-order: $i;
				order: $i;
			}
		}
	}
}

/* Flexbbox */ 
@mixin d-flex {
  display: -webkit-box;
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

@mixin flex-center {
  @include d-flex;
  justify-content: center;
  align-items: center;
}

@mixin flex-center-x {
  @include d-flex;
  justify-content: center;
}

@mixin n-flex-center-x {
  justify-content: center;
}

@mixin flex-center-y {
  @include d-flex;
  align-items: center;
}

@mixin n-flex-center-y {
  align-items: center;
}

@mixin flex-between {
  @include d-flex;
  justify-content: space-between;
}

@mixin n-flex-between {
  justify-content: space-between;
}

@mixin flex-left {
  @include d-flex;
  justify-content: flex-start;
}

@mixin n-flex-left {
  justify-content: flex-start;
}

@mixin flex-right {
  @include d-flex;
  justify-content: flex-end;
}

@mixin n-flex-right {
  justify-content: flex-end;
}