/* =============
   Button
============= */

.btn {
    padding: 5px 16px;
    // padding: 0.7em 2em;
    border-radius: $border-theme;
    font-family: $font-1-medium;
    outline: none;
    font-size: 14px;
    text-transform: uppercase;
    // @include box-shadow($shadow);
    @include transition(#{'box-shadow .2s cubic-bezier(.4, 0, 1, 1), background-color .2s cubic-bezier(.4, 0, .2, 1)'});

    &:hover,
    &:focus,
    &:active {
        // @include box-shadow($shadow-focus);
        box-shadow: none;
        outline: none;
    }

    &.btn-round {
        border-radius: $rounded;
    }

    &.btn-just-icon {
        &.btn-sm {
            padding: 7px;
        }

        &.btn-lg {
            padding: 17px;
        }

        font-size: 20px;
        padding: 11px;
        line-height: 1em;
        text-align: center;

        >i {
            width: 20px;
            height: 20px;
            position: relative;
            margin: 0;
        }
    }
}

.btn-group-sm>.btn,
.btn-sm {
    padding: 0.3em 1.6em;
}



@for $i from 1 through length($base-social-color) {

    @each $hex,
    $name in nth($base-social-color, $i) {

        .btn-#{$name}:not(:disabled):not(.disabled):not(.btn-link).active,
        .btn-#{$name}:not(:disabled):not(.disabled):not(.btn-link):active,
        .show>.btn-#{$name}.dropdown-toggle {
            @if $name=='white'or $name=='light' {
                color: $text-primary;
            }

            @else {
                color: $white;
            }

            background-color: darken($hex, 5%);
            border-color: darken($hex, 10%);
        }

        .btn-#{$name}:not(:disabled):not(.disabled):not(.btn-link).active:focus,
        .btn-#{$name}:not(:disabled):not(.disabled):not(.btn-link):active:focus,
        .show>.btn-#{$name}.dropdown-toggle:focus {
            // @include box-shadow($shadow-focus);
        }

        .btn-#{$name}:not(.btn-link) {
            @if $name=='white'or $name=='light' {
                color: $text-primary;
                border: 1px solid rgba($black, .1);
            }

            @else {
                color: $white;
            }

            background-color: $hex;
            border-color: $hex;
        }

        .btn-outline-#{$name}:not(:disabled):not(.disabled).active,
        .btn-outline-#{$name}:not(:disabled):not(.disabled):active,
        .show>.btn-outline-#{$name}.dropdown-toggle {
            color: $white;
        }

        .btn-outline-#{$name}:not(:disabled):not(.disabled).active:focus,
        .btn-outline-#{$name}:not(:disabled):not(.disabled):active:focus,
        .show>.btn-outline-#{$name}.dropdown-toggle:focus {
            @include box-shadow($shadow-focus);
        }

        .btn-outline-#{$name} {
            color: $hex;
            border-color: $hex;
            // background-color: $white;
            background-color: transparent;

            &:hover {
                color: $white;
                background-color: $hex;
                border-color: $hex;
            }
        }

        .btn-outline-#{$name}-i:not(:disabled):not(.disabled).active,
        .btn-outline-#{$name}-i:not(:disabled):not(.disabled):active,
        .show>.btn-outline-#{$name}-i.dropdown-toggle {
            color: $name;
        }

        .btn-outline-#{$name}-i:not(:disabled):not(.disabled).active:focus,
        .btn-outline-#{$name}-i:not(:disabled):not(.disabled):active:focus,
        .show>.btn-outline-#{$name}-i.dropdown-toggle:focus {
            // @include box-shadow($shadow-focus);
            box-shadow: none;
        }

        .btn-outline-#{$name}-i {
            color: $white;
            background-color: $hex;
            border-color: $hex;

            &:hover {
                color: $hex;
                background-color: transparent;
                border-color: $hex;
            }

            .buttonText {
                color: $white;
            }
        }
    }
}

.btn-link {
    box-shadow: none;

    @for $i from 1 through length($base-social-color) {

        @each $hex,
        $name in nth($base-social-color, $i) {
            @if $name !='white'and $name !='light' {
                &.btn-#{$name} {
                    color: $hex;
                    border-color: transparent;

                    &:hover,
                    &:focus,
                    &:active {
                        // @if $name == 'warning' {
                        // 	color: $text-primary;
                        // }
                        // @else {
                        // 	color: $hex;
                        // }
                        color: $hex;
                        text-decoration: none;
                        background-color: rgba(0, 0, 0, 0.1);
                        box-shadow: none;
                    }
                }

                &.btn-#{$name}:not(:disabled):not(.disabled).active,
                &.btn-#{$name}:not(:disabled):not(.disabled):active,
                &.show>.btn-#{$name}.dropdown-toggle {
                    @if $name=='warning' {
                        color: $text-primary;
                    }

                    @else {
                        color: $hex;
                    }

                    color: $hex;
                    background-color: rgba(0, 0, 0, 0.1);
                    border-color: transparent;
                    box-shadow: none;
                }

                &.btn-#{$name}:not(:disabled):not(.disabled).active:focus,
                &.btn-#{$name}:not(:disabled):not(.disabled):active:focus,
                &.show>.btn-#{$name}.dropdown-toggle:focus {
                    box-shadow: none;
                }
            }

            &.btn-default {
                box-shadow: none;
                color: $text-primary;
                border-color: transparent;

                &:hover,
                &:focus,
                &:active {
                    text-decoration: none;
                    box-shadow: none;
                    background-color: rgba(0, 0, 0, 0.1);
                }
            }

            &.btn-default:not(:disabled):not(.disabled).active,
            &.btn-default:not(:disabled):not(.disabled):active,
            &.show>.btn-default.dropdown-toggle {}

            &.btn-default:not(:disabled):not(.disabled).active:focus,
            &.btn-default:not(:disabled):not(.disabled):active:focus,
            &.show>.btn-default.dropdown-toggle:focus {}
        }
    }
}

