.btn-effect {
    position: relative;
    transition: 0.3s;
    border: solid 2px transparent;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    border-radius: 2px;

    &:before {
        content: '';
        display: inline-block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        border: solid 3px transparent;
        border-radius: 5px;
    }

    &:hover {
        background-color: #fff !important;
        color: #f7941d;
        border: solid 2px #f7941d;
        cursor: pointer;

        &:before {
            width: calc(100% + 30px);
            height: calc(100% + 30px);
            top: -15px;
            left: -15px;
            border: solid 8px #F7951F;
            opacity: 0;
            visibility: hidden;
            transition: 0.7s;
        }
    }
}