.header-menu {
	position: sticky;
	top: 0;
	z-index: 9;
	background-color: $white;
	box-shadow: 0 0px 20px rgba(0, 0, 0, 0.04);

	.header-row {
		justify-content: center;

        // padding: 30px 0 12px 0;
        .header-side {
        	@include flex-center;

        	.language,
        	.social-icon {
        		display: block;
        		text-decoration: none;
        	}

        	.social-icon {
        		>i {
        			font-size: 24px;

                    // @include gradient-primary-color-hover;
                    @include media-breakpoint-down(sm) {
                    	font-size: 18px;
                    }
                }

                &:first-child {
                	margin: 0 30px 0;

                	@include media-breakpoint-down(sm) {
                		margin: 0 10px 0;
                	}
                }
            }

            .language {
            	text-transform: uppercase;
            	font-size: inherit;
            	color: $text-secondary;

            	&.active {
                    // @include gradient-primary-color-hover;
                }

                &:first-child {
                	margin: 0 20px 0;
                }
            }
        }
    }

}

.navigation {
	background: $white;

	>ul {
		min-height: 74px;
		padding: 0;
		margin: 0;
		list-style-type: none;
		position: relative;
		@include flex-center-y;

		>li {
			position: relative;

			&:last-child {
				>a {
					border-right: none;
				}
			}

			>a {
				padding: 29px 30px;
				border-right: 1px solid #ebebeb;
				color: #454545;
				text-decoration: none;
				text-transform: uppercase;
				font-family: $font-1-medium;
				@include a-hover($custom);
				@include transition(#{'color .3s ease'});
			}

			>.sub-menu {
				.has-children {
					>a {
						padding-right: 35px !important;

						&::after {
							display: block;
							position: absolute;
							top: 50%;
							right: 18px;
							width: 0;
							height: 0;
							margin-top: -4px;
							border-top: .3em solid transparent;
							border-bottom: .3em solid transparent;
							border-left: .32em solid;
							content: '';
						}
					}
				}
			}

			&:hover {
				>.mega-menu {
					display: table;
					-webkit-animation: megamenu-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);
					animation: megamenu-show 0.45s cubic-bezier(0.68, -0.55, 0.265, 1.55);

					.sub-menu {
						-webkit-animation: none;
						animation: none
					}
				}
			}

			&.mobile {
				@include media-breakpoint-up(xl) {
					display: none;
				}
			}

			&.logo {
				position: absolute;
				left: 50%;
				top: 50%;
				transform: translate(-50%, -50%);

				a {
					display: block;
					padding: 0;

					img {
						max-width: 80px;
						width: 100%;

						@include media-breakpoint-down(sm) {
							max-width: 60px;
						}
					}
				}

				+li {
					margin-left: auto;

					>a {
						border-left: 1px solid #ebebeb;
					}
				}
			}

			&.search-form {
				padding: 18px 30px;

				.input-group {
					.input-group-text {
						color: $muted1;
						cursor: pointer;
						border: none;
						background-color: transparent;
					}

					.form-control {
						border-top: none;
						border-left: none;
						border-right: none;
						width: 0;
						padding: 0;
						@include transition(all .3s);

						&:focus {
							box-shadow: none;
						}
					}
				}

				&.focus {
					.input-group {
						.form-control {
							width: 100px;
						}
					}
				}
			}
		}

		>li.has-children,
		>li.has-megamenu {
			>a {
				&:after {
					display: inline-block;
					width: 0;
					height: 0;
					margin-left: .255em;
					vertical-align: .255em;
                    // content: "";
                    border-top: .3em solid;
                    border-right: .3em solid transparent;
                    border-bottom: 0;
                    border-left: .3em solid transparent;
                    position: relative;
                    bottom: 0;
                }
            }
        }
    }

    ul>li:hover>.sub-menu {
    	visibility: visible;
    	@include opacity(1);
    	z-index: 9;
    }
}

@include media-breakpoint-down(lg) {
	.navigation {
		ul {
			>li {
				&:not(.logo):not(.mobile) {
					display: none;
				}
			}
		}
	}
}

@include media-breakpoint-down(sm) {
	.header-mid {
		flex: 0 0 100%;
		max-width: 100%;

		.navigation {
			width: 100%;
		}
	}
}


.sub-menu {
	visibility: hidden;
	@include opacity(0);
	margin-top: -7px !important;
	padding: 0 !important;
	background-color: $white;
	line-height: 1.5;
	z-index: -1;
	position: absolute;
	top: 54px;
	left: -2px;
	min-width: 250px;
	@include transition(#{'visibility .3s ease, opacity .3s ease, z-index .3s ease'});

	>li {
		display: block;
		position: relative;

        // border-bottom: 1px solid #e7eaef;
        >a {
        	display: block;
        	padding: 15px !important;
        	color: #454545;
        	font-size: 12px;
        	text-align: left;
        	text-decoration: none;
        	@include transition(#{'.1s all ease'});

        	&:hover {
        		color: $white;
        		background-color: $custom;
        	}
        }

        &:last-child {
        	border-bottom: none;
        }

        >.sub-menu {
        	top: -4px;
        	left: 100%;
        	margin-left: -5px;
        }
    }
}

#header {
	background-color: #fff;

	-webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);
	box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);

	.btn-search-mobile {
		background: #fff !important;
		border: none !important;

		@media (min-width: 992px) {
			display: none;
		}
	}

	.top {
		border-bottom: 1px solid #e2e2e2;
		transition: height .25s linear, zIndex 0s linear .25s;
		transition: all .3s;
        // height: 53px;
        height: 120px;
        padding: 2px 0;

        @media (max-width: 575px) {
        	display: none;
        }

        .nav {
        	li.nav-item {
        		&:not(:first-child) {
        			margin-left: 15px;

        			a {
        				&:hover {
        					color: $custom;
        				}
        			}
        		}

        		a {
        			font-size: 13px;
        			color: #2e3133;
        			font-family: 'FranklinGothic Medium';
        			transition: all .3s;
        		}

        		&.search {
        			a {
        				position: relative;
        				padding-left: 23px;

        				&:before {
        					content: '';
        					position: absolute;
        					background: url("../img/icon/search.png") no-repeat;
        					width: 19px;
        					height: 19px;
        					background-size: 100%;
        					display: inline-block;
        					top: calc((100% - 19px)/2);
        					left: 0;
        					display: none;
        				}

        				.input-group {
        					.form-control {
        						-webkit-border-radius: 0;
        						-moz-border-radius: 0;
        						border-radius: 0;

        						font-size: 13px;
        						color: #2e3133;
        						font-family: 'FranklinGothic Medium';
        						padding-left: 0;
        						border-left: none;
        						border-right: none;
        						border-top: none;
        						width: 55px;
        						-webkit-transition: width 400ms ease, background 400ms ease;
        						transition: width 400ms ease, background 400ms ease;
        						border-color: transparent;

        						&::-webkit-input-placeholder {
                                    /* Chrome/Opera/Safari */
                                    color: #2e3133;
                                }

                                &:focus {
                                	outline: none;
                                	-webkit-box-shadow: none;
                                	-moz-box-shadow: none;
                                	box-shadow: none;
                                	width: 200px;
                                	border-color: #80bdff;
                                }

                            }

                            &:focus {
                            	.form-control {
                            		width: 200px;
                            		border-color: #80bdff;
                            	}
                            }

                            .input-group-btn {
                            	&:focus~.form-control {
                            		width: 200px;
                            		border-color: #80bdff;
                            	}

                            	.btn {
                            		font-size: 13px;
                            		color: #2e3133;
                            		font-family: 'FranklinGothic Medium';
                            		-webkit-border-radius: 0;
                            		-moz-border-radius: 0;
                            		border-radius: 0;
                            		background-color: transparent;
                            		border: none;
                            		padding-left: 0;

                            		&:focus {
                            			outline: none;
                            			-webkit-box-shadow: none;
                            			-moz-box-shadow: none;
                            			box-shadow: none;
                            		}
                            	}
                            }
                        }
                    }
                }

                &.phone {
                	a {
                		position: relative;
                		padding-left: 20px;

                		&:before {
                			content: "\f095";
                			font-family: 'Font Awesome 5 Free';
                			font-weight: 900;
                			position: absolute;
                			top: 8px;
                			left: 0;
                		}
                	}
                }

                &.login {
                	a {
                		position: relative;
                		padding-left: 20px;

                		&:before {
                			content: '';
                			background: url("../img/icon/profile.png") no-repeat;
                			background-size: 100%;
                			width: 15px;
                			height: 14px;
                			display: inline-block;
                			position: absolute;
                			top: 8px;
                			left: 0;
                		}
                	}
                }

                &.contact {
                	a {
                		border: 1px solid $custom;
                		color: $custom;
                		text-transform: uppercase;
                		padding: 6px 9px;
                		-webkit-border-radius: 4px;
                		-moz-border-radius: 4px;
                		border-radius: 4px;
                	}
                }
            }
        }
    }

    &.fixed-top {
    	top: 0;
    	left: 0;
    	width: 100%;
    	z-index: 16;
    	background-color: rgba(255, 255, 255, 0.97);
    	-webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);
    	box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);

    	.top {
    		height: 0 !important;
    		padding: 0;
    		opacity: 0;
    	}
    }

    .bottom {
    	.navbar-brand {
    		img {
    			@media (max-width: 575px) {
    				height: 40px;
    			}
    		}
    	}

    	padding-top: 5px;

    	@media (max-width: 575px) {
    		padding-top: 5px;
    		padding-bottom: 5px;
    	}

    	.navbar-toggler {
    		-webkit-border-radius: 0;
    		-moz-border-radius: 0;
    		border-radius: 0;
    		border: none;
    		padding-right: 0;

    		&:focus {
    			outline: none;
    			-webkit-box-shadow: none;
    			-moz-box-shadow: none;
    			box-shadow: none;
    		}
    	}
    }

    hr {
    	margin: 7px 0;
    }

    .navbar {
    	padding: 0;

    	.navbar-nav {
    		height: 100%;
    		@media (max-width: 991px) {
    			display: none;
    		}

    		.dropdown-menu {
    			width: 100%;
    			margin: 0!important;
    			-webkit-border-radius: 0;
    			-moz-border-radius: 0;
    			border-radius: 0;
    			border: none;
    			-webkit-box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);
    			box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.06), 0 4px 5px 0 rgba(0, 0, 0, 0.06), 0 1px 10px 0 rgba(0, 0, 0, 0.08);
    			padding: 0;
    			transition: all .3s;

    			.box_mega {
    				border-top: 4px solid $custom;
    				min-height: 200px;

    				.box {
    					padding: 30px 40px;
    					flex: 1;

    					.title {
    						margin: 0;
    						margin-bottom: 20px;
    						font-family: 'FranklinGothic Demi';
    						font-size: 20px;
    					}

    					.nav {
    						.nav-link {
    							font-size: 14px;
    							font-family: 'FranklinGothic Book';

    							&:hover {
    								background-color: rgba($custom, .6);
    								color: #fff;
    							}
    						}
    					}
    				}

    				.overlight {
    					position: absolute;
    					background: #00000047;
    					width: 100%;
    					height: 100%;

    					a {
    						font-size: 24px;
    						color: #fff;
    						text-transform: uppercase;
    						font-family: "FranklinGothic Demi";
    					}
    				}
    			}

    			img {
    				height: 308px;
    				height: 100%;

    				@media (max-width: 768px) {
    					display: none !important;
    				}
    			}

    			&.dropdown-menu-type-3 {
    				width: auto;
    				left: auto;

    				.box_mega {
    					min-height: auto;

    					.box {
    						padding: 0;
    					}
    				}
    			}
    		}

    		>li {
    			position: static;

    			&+li {
    				@media (max-width: 768px) {
    					border-top: 1px solid #e4e4e4;
    				}
    			}

    			>a {

    				text-transform: uppercase;
    				font-size: 14px;
    				font-family: 'FranklinGothic Medium';
    				height: 100%;
    				display: inline-flex;
    				align-items: center;
    				padding-right: 15px;
    				padding-left: 15px;
    				// min-height: 70px;

    				@media (min-width: 992px) {
    					position: relative;

    					&:before {
    						content: '';
    						position: absolute;
    						left: 15px;
    						right: 15px;
    						// top: 10px;
                            top: -7px;
    						background-color: $custom;
    						height: 3px;
    						width: 0;
    						-webkit-transition: all .3s ease;
    						-moz-transition: all .3s ease;
    						-ms-transition: all .3s ease;
    						-o-transition: all .3s ease;
    					}

    					&:hover {
    						&:before {
    							width: calc(100% - 30px);
    						}
    					}
    				}

    				@media (max-width: 575px) {
    					display: block;
    					padding-right: 15px;
    					padding-left: 15px;
    				}

    				&.dropdown-toggle {
    					&:after {
    						@media (max-width: 768px) {
    							float: right;
    							margin-top: 9px;
                                //display: none;
                            }
                        }
                    }
                }

                &.show {
                	.dropdown-menu {
                		display: block;
                		top: calc(100% - 0px);

                		@media (max-width: 980px) {
                			padding: 0 15px;
                		}
                	}
                }
            }
        }
    }
}