.gallery-wrapper{
	.gallery-item{
		margin-bottom: 30px;
		.gallery-title{
			font-size: 16px;
			text-transform: uppercase;
		}
		.gallery-thumb{
			position: relative;
			margin-bottom: 20px;
			&:hover{
				.gallery-backdrop{
					opacity: 1;
					visibility: visible;
				}
			}
		}
		.gallery-backdrop{
			background-color: rgba($black, .70);
			opacity: 0;
			visibility: hidden;
			@include transition(opacity .3s ease, visibility .3s ease);
			@include position-full;
			@include flex-center;
		}
		
	}
}