.bg-overlay{
	position: relative;
	.bg-overlay-title{
		z-index: 1;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}
}

.banner-img{
	position: relative;
	z-index: 1;
	>img{
		width: 100%;
		@include media-breakpoint-down(sm) {
			min-height: 450px;
			-o-object-fit: cover;
			object-fit: cover;
		}
	}
	.banner-title{
		font-family: $font-1-demi;
		font-size: 40px;
		text-transform: uppercase;
		color: $white;
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
		text-shadow: 2.5px 3px 2px #333;
	}
}


.banner-slider{
	.slick-next, .slick-prev {
		width: auto;
		height: auto;
		z-index: 1;
		&:before{	
			content: " ";
			width: 35px;
			height: 59px;
			display: block;
			background-repeat: no-repeat;
			background-color: rgba($white, .9);
			background-size: 70% 70%;
			background-position: 8px;
		}
	}
	.slick-next {
		right: 0;
		&:before{	
			background-image: url(../img/icon/slider-next.png);
		}
	}
	.slick-prev {
		left: 0;
		&:before{	
			background-image: url(../img/icon/slider-prev.png);
		}
	}
}


.gallery-slider{
	.slick-next, .slick-prev {
		width: auto;
		height: auto;
		z-index: 1;
		&:before{	
			content: " ";
			width: 35px;
			height: 59px;
			display: block;
			background-repeat: no-repeat;
		}
	}
	.slick-next {
		right: -50px;
		&:before{	
			background-image: url(../img/icon/slider-next.png);
		}
	}
	.slick-prev {
		left: -50px;
		&:before{	
			background-image: url(../img/icon/slider-prev.png);
		}
	}
	@include media-breakpoint-down(lg) {
		.slick-next, .slick-prev {
			&:before{
				background-color: rgba($white, .9);
				background-size: 70% 70%;
				background-position: 8px;
			}
		}
		.slick-next {
			right: 25px;
		}
		.slick-prev {
			left: 25px;
		}
	}
	@include media-breakpoint-down(sm) {
		.slick-next {
			right: 0;
		}
		.slick-prev {
			left: 0;
		}
	}
}

