table, th, td {
	border: none;
}

table {
	width: 100%;
	display: table;
	&.bordered > {
		thead > tr, tbody > tr {
			border-bottom: 1px solid #d0d0d0;
		}
	}
	&.striped > tbody > tr {
		&:nth-child(odd) {
			background-color: #f2f2f2;
		}
		> td {
			border-radius: 0;
		}
	}
	&.highlight {
		> tbody {
			> tr {
				transition: background-color 0.25s ease;
				&:hover {
					background-color: #f2f2f2;
				}
			}
		}
	}
	&.centered {
		thead, tbody{
			tr{
				th, td{
					text-align: center;
				}
			}
		}
	}
}

thead {
	border-bottom: 1px solid #d0d0d0;
}

td, th {
	padding: 15px 5px;
	display: table-cell;
	text-align: left;
	vertical-align: middle;
	border-radius: 2px;
}

@media only screen and (max-width: 992px) {
	table.responsive-table {
		width: 100%;
		border-collapse: collapse;
		border-spacing: 0;
		display: block;
		position: relative;
		td:empty:before {
			content: " ";
		}
		th, td {
			margin: 0;
			vertical-align: top;
		}
		th {
			text-align: left;
		}
		thead {
			display: block;
			float: left;
			tr {
				display: block;
				padding: 0 10px 0 0;
				th::before {
					content: " ";
				}
			}
		}
		tbody {
			display: block;
			width: auto;
			position: relative;
			overflow-x: auto;
			white-space: nowrap;
			tr {
				display: inline-block;
				vertical-align: top;
			}
		}
		th {
			display: block;
			text-align: right;
		}
		td {
			display: block;
			min-height: 1.25em;
			text-align: left;
		}
		tr {
			padding: 0 10px;
		}
		thead {
			border: 0;
			border-right: 1px solid #d0d0d0;
		}
		&.bordered {
			th {
				border-bottom: 0;
				border-left: 0;
			}
			td {
				border-left: 0;
				border-right: 0;
				border-bottom: 0;
			}
			tr {
				border: 0;
			}
			tbody {
				tr {
					border-right: 1px solid #d0d0d0;
				}
			}
		}
	}
}

.table-theme{
	&.bordered{
		font-family: $font-1-demi;
		thead{
			tr{
				th{
					
					color: #2e3133;
					font-size: 16px;
				}
			}
		}
		tbody{
			>tr{
				&:last-child{
					border-bottom: none;
				}
			}
		}
		.table-category{
			height: 44px;
			line-height: 44px;
			position: relative;
			&:before {
				content: attr(data-title);
				position: absolute;
				left: 15px;
				right: 15px;
				text-align: center;
				text-transform: uppercase;
				color: white;
				font-family: $font-1-demi;
				font-size: 16px;
				background-color: #2e3133;
				letter-spacing: 2px;
			}
		}
		&.s2{
			background-color: $white;
			border-radius: $border-theme;
			@include box-shadow-hover;
			@include transition(box-shadow .3s ease);
			tr{
				>td, >th{
					&:first-child{
						padding-left: 20px;
					}
					&:last-child{
						padding-right: 20px;
					}
				}
			}
			thead{
				tr{
					border-bottom: 1px solid $custom;
					>th{
						padding-top: 25px;
					}
				}
			}
		}
	}
}