/* Font */
@each $var,$name in $base-theme-fonts {
	.#{$name} {
		font-family: $var !important;
	}
}
.font-inherit{
	font-size: inherit;
}

.font-weight-semi {
    font-weight: $font-weight-semi!important;
}

.font-weight-light {
    font-weight: $font-weight-light!important;
}

.text-underline{
	text-decoration: underline;
}

.tooltip{
	font-family: inherit;
}
.h1, .h2, .h3, .h4, .h5, .h6, h1, h2, h3, h4, h5, h6{
}

/* Text colors */
@each $hex,$name in $base-color {
	.text-#{$name} {
		color: $hex !important;
	}
}
.text-muted{
	color: $text-secondary!important;
}

.text-blur{
	filter: blur(5px);
}

a{
	color: $blue;
	transition: color .3s;
	&:hover{
		color: $blue;
	}
}