$base-font-size : 14px;

$base-theme-fonts : ($font-1-medium, 'font-1-medium'), ($font-1-book, 'font-1-book'), ($font-1-demi, 'font-1-demi'), ($font-1-italic, 'font-1-italic');
$base-color : ($custom, 'custom'), ($muted1, 'muted1'), ($muted2, 'muted2'), ($primary, 'primary'), ($secondary, 'secondary'), ($blue, 'blue'), ($indigo, 'indigo'), ($purple, 'purple'), ($pink, 'pink'), ($red, 'red'), ($orange, 'orange'), ($yellow, 'yellow'), ($green, 'green'), ($teal, 'teal'), ($cyan, 'cyan'), ($white, 'white'), ($black, 'black'), ($gray, 'gray'), ($gray-dark, 'gray-dark'), ($success, 'success'), ($info, 'info'), ($warning, 'warning'), ($danger, 'danger'), ($light, 'light'), ($dark, 'dark');
$social-color : ($twitter, 'twitter'), ($youtube, 'youtube'), ($facebook, 'facebook'), ($google, 'google'), ($linkedin, 'linkedin'), ($pinterest, 'pinterest'), ($dribbble, 'dribbble'), ($behance, 'behance'), ($tumblr, 'tumblr'), ($github-alt, 'github-alt'), ($reddit, 'reddit');

$base-social-color : $social-color, $base-color;

$datepicker-color-days:             rgba(255,255,255, .8)  !default;
$datepicker-color-old-new-days:     rgba(255,255,255, .4)  !default;

$opacity-1:                  rgba(255,255,255, .1) !default;
$opacity-2:                  rgba(255,255,255, .2) !default;

$general-transition-time:  300ms             !default;
$transition-ease:           ease 0s;

/* Material Style */
@function md-shadow($hex, $darken) {
    @if $darken == 'undefined' {
        @return 0 0 8px rgba($hex, .12), 0 6px 8px rgba($hex, .24);
    }
    @else{
        @return 0 0 ($darken * 2px) rgba($hex, .12), 0 ($darken * 2px) ($darken * 2px) rgba($hex, .24);
    }
}

// Shadow
// $shadow: md-shadow($black, 1);
// $shadow-focus: md-shadow($black, 4);

// $shadow-1: $shadow-focus;
// $shadow-1-focus: md-shadow($black, 8);

$shadow: md-shadow(#888, 1);
$shadow-focus: md-shadow(#888, 2);

$shadow-1: $shadow-focus;
$shadow-1-focus: md-shadow(#888, 4);