.footer{
	background-color: #fbfbfb;
	.footer-title{
		color: $muted1;
		font-size: 16px;
		font-family: $font-1-medium;
		text-transform: uppercase;
	}
	.footer-content{
		margin-top: 15px;
		margin-bottom: 15px;
	}
	.footer-social{
		>a, .address{
			color: $muted1;
			padding-right: 20px;
		}
		.address{
			padding-left: 20px;
			border-left: 1px solid $muted1;
			@include media-breakpoint-down(sm) {
				padding-left: 0;
				padding-top: 10px;
				border-left: 0;
				display: block;
			}
		}
	}
	.footer-link{
		display: flex;
		flex-wrap: wrap;
		margin: 0;
		padding: 0;
		>li{
			list-style-type: none;
			@include media-breakpoint-down(sm) {
				margin-bottom: 10px;
			}
			>a{
				color: #dfdfdf;
				padding: .5rem 1rem;
				font-family: $font-1-book;
				text-transform: uppercase;
				position: relative;
			}
			&:not(:last-child){
				>a{
					border-right: 1px solid #dfdfdf;
				}
			}
		}
	}
}

