.offcanvas-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 290px;
    height: 100%;
    background-color: $gray;
    box-shadow: inset -4px 0 17px 0 rgba(darken($gray, 8%), 1);
    visibility: hidden;
    z-index: 1;
    overflow-y: auto;
}

.offcanvas-container.active {
    visibility: visible;
}

.offcanvas-menu {
    position: relative;
    width: 100%;
    overflow: hidden;
    ul {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            display: block;
            a {
                display: block;
                padding: 12px 20px;
                transition: color .3s;
                border-bottom: 1px solid rgba(255, 255, 255, 0.12);
                color: $white;
                font-size: 14px;
                font-weight: 500;
                letter-spacing: .05em;
                // text-transform: uppercase;
                text-decoration: none;
                &:hover {
                    color: $white;
                    // &:before{
                    //     margin-right: 5px;
                    //     content: "\f18e";
                    //     display: inline-block;
                    //     font: normal normal normal 14px/1 FontAwesome;
                    //     font-size: inherit;
                    //     text-rendering: auto;
                    //     -webkit-font-smoothing: antialiased;
                    //     -moz-osx-font-smoothing: grayscale;
                    // }
                }
                &:before{
                    @include general-transition;
                    content: '';
                }
            }
        }
        li.back-btn{
            >a {
                background-color: $dark;
                color: $white;
                &::before {
                    display: inline-block;
                    width: 0;
                    height: 0;
                    margin-right: 8px;
                    border-top: .32em solid transparent;
                    border-right: .37em solid;
                    border-bottom: .32em solid transparent;
                    content: '';
                    opacity: .75;
                }
            }
        }
        li.active>a {
            color: $color-primary;
        }
        li.has-children, li.has-megamenu{
            >span{
                display: block;
                position: relative;
                width: 100%;
                >a{
                    padding-right: 65px;
                }
            }
            .sub-menu-toggle {
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                width: 60px;
                height: 100%;
                height: calc(100% - 1px);
                transition: background-color .3s;
                border-left: 1px solid rgba(255, 255, 255, 0.12);
                color: rgba(255, 255, 255, 0.75);
                cursor: pointer;
                z-index: 1;
                &::before {
                    display: block;
                    position: absolute;
                    top: 50%;
                    right: 25px;
                    width: 0;
                    height: 0;
                    margin-top: -5px;
                    border-top: .35em solid transparent;
                    border-bottom: .35em solid transparent;
                    border-left: .4em solid;
                    content: '';
                }
                &:hover {
                    background-color: rgba(255, 255, 255, 0.1);
                }
            }
            >.offcanvas-submenu {
                position: absolute;
                top: 0;
                right: -100%;
                width: 100%;
                height: auto;
                transform: translate3d(100%, 0, 0);
                transition: all 0.4s cubic-bezier(0.86, 0, 0.07, 1);
            }
            >.offcanvas-submenu.in-view {
                transform: translate3d(0, 0, 0);
            }
            >.offcanvas-submenu.off-view {
                transform: translate3d(-100%, 0, 0);
            }
        }
        li.has-children.active{
            >span{
                >a{
                    color: $color-primary;
                }
            }
        }
    }
    ul.menu {
        position: relative;
        transform: translate3d(0, 0, 0);
        transition: all 0.4s cubic-bezier(0.86, 0, 0.07, 1);
    }
    ul.menu.off-view {
        transform: translate3d(-100%, 0, 0);
    }
    ul.menu.in-view {
        transform: translate3d(0, 0, 0);
    }
}

.offcanvas-wrapper {
    position: relative;
    min-height: 100vh;
    background-color: $white;
    z-index: 10;
}



.offcanvas-header {
    padding: 16px 20px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.12);
    background-color: darken($gray, 8%);
    .offcanvas-title {
        margin-bottom: 0;
        color: rgba($white, 1);
        font-size: 16px;
        font-weight: 700;
        text-transform: uppercase;
    }
}

.offcanvas-wrapper{
    -webkit-transition: -webkit-transform .4s ease-in-out, background-color .2s;
    transition: transform .4s ease-in-out, background-color .2s;
}


.offcanvas-open{
    .offcanvas-wrapper, .site-backdrop{
        -webkit-transform: translate3d(290px, 0, 0);
        transform: translate3d(290px, 0, 0);
    }
    .site-backdrop {
        opacity: .2;
        visibility: visible;
    }
}



.site-backdrop {
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $white;
    content: '';
    opacity: 0;
    z-index: 9980;
    position: fixed;
    transition: transform .4s ease-in-out, opacity .35s, visibility .35s;
    cursor: pointer;
    visibility: hidden;
}