/* Tab */
.tab-content>.active {
	animation: fadeIn .3s linear;
}

.nav-tabs{
	.nav-link{
		font-weight: 400;
		text-transform: uppercase;
		color: $text-secondary;
		@include transition(#{'all .3s'});
	}
	.nav-item.show .nav-link, .nav-link.active{
		color: $text-primary;
	}
}

.nav-pills{
	.nav-link{
		font-weight: 400;
		text-transform: uppercase;
		color: $text-secondary;
		@include transition(#{'all .3s'});
		&.rounded{
			border-radius: $rounded!important;
		}
	}
	.nav-link:not(.active){
		&:hover{
			background-color: rgba($text-disabled, .05);
		}
	}
	.nav-item.show .nav-link, .nav-link.active{
		color: $white;
		@include box-shadow($shadow-focus);
	}
	&.with-icon{
		.nav-item{
			.nav-link{
				>i{
					display: block;
					text-align: center;
					font-size: 20px;
					line-height: 30px;
				}
			}
		}
	}
}

@each $hex,$name in $base-color {
	.nav-pills.nav-#{$name}{
		.nav-item.show .nav-link, .nav-link.active{
			background-color: $hex;
		}
	}
}

@each $hex,$name in $base-color {
	.nav-#{$name}:not(.nav-pills){
		.nav-link{
			position: relative;
			font-weight: $font-weight-normal;
			color: $text-secondary;
			@include transition(#{'all .3s'});
			&:before{
				content: "";
				height: 2px;
				position: absolute;
				width: 100%;
				left: 0;
				bottom: 0;
				background-color: $hex;
				transform: scale(0);
				transition: all .3s;
			}
			&.active{
				&:before{
					transform: scale(1);
				}
			}
		}
		.nav-item.show .nav-link, .nav-link.active{
			color: $hex;
		}
	}
}

.nav:not(.nav-tabs):not(.nav-pills){
	border-bottom: 1px solid rgba($muted, .2);
}
.nav:not(.with-icon){
	.nav-item{
		.nav-link{
			>i{
				margin-right: 5px;
			}
		}
	}
}

/* Tabs Left*/
.nav-left{
	display: flex;
	flex-wrap: wrap;
	>.nav{
		display: block;
		.nav-item{
			margin-bottom: 10px;
			.nav-link{
				min-width: 160px;
			}
		}
	}
	.tab-content{
		flex: 0 0 calc(100% - 160px);
		max-width: calc(100% - 160px);
	}
	@include media-breakpoint-down(lg) {
		display: block;
		>.nav{
			display: flex;
			justify-content: center;
			.nav-item{
				-ms-flex: unset;
				flex: unset;
				margin-bottom: 20px;
			}
		}
		.tab-content{
			flex: 0 0 100%;
			max-width: 100%;
		}
	}
}

.nav{
	&.nav-theme{
		.nav-item{
			.nav-link{
				color: $muted1;
			}
		}
	}
}
.nav-pills{
	.nav-link{
		border-radius: $border-theme;
		padding: 2.47rem 1rem;
	}
	&.nav-small{
		.nav-link{
			padding: 5px 16px;
		}
	}
	&.with-icon{
		.nav-item{
			.nav-link{
				>i{
					font-size: 40px;
					line-height: unset;
				}
			}
		}
	}
	&.nav-white{
		.nav-item.show .nav-link, .nav-link.active{
			color: $muted1;
		}
	}
}


/* Custom Navbar */
.nav-pills-custom{
	.nav-link{
		font-weight: 400;
		text-transform: uppercase;
		color: $text-secondary;
		background-color: $white;
		@include box-shadow(0 0 4px rgba(136,136,136,0.12), 0 0px 4px rgba(136,136,136,0.24));
		@include transition(#{'all .3s'});
		border-radius: 1rem;
	}
	.nav-link:not(.active){
		&:hover{
			background-color: rgba($text-disabled, .05);
		}
	}
	.nav-item.show .nav-link, .nav-link.active{
		color: $white;
		background-color: $custom;
	}
	&.with-icon{
		.nav-item{
			.nav-link{
				>i{
					display: block;
					text-align: center;
					font-size: 20px;
					line-height: 30px;
				}
			}
		}
	}
}

.nav-left{
	.nav-pills-custom{
		.nav-item{
			.nav-link{
				width: 160px;
			}
		}
	}
}