.notification{
	position: relative;
	width: 100%; 
	color: $white;
	padding: 30px 50px 30px 30px;
	&.notification-custom-overlay{
		background: rgba($custom, .8); 
	}
	.close-notification{
		position: absolute;
		right: 25px;
		top: 50%;
		transform: translateY(-50%);
		color: $white;
	}
}

.notification-header{
	.content {
		position: relative;
	}
	.text-content {
		margin: 0 150px 0 0;
		@include media-breakpoint-down(sm) {
			margin: 0 0 20px 0;
		}
	}
	.btn-dismiss {
		position: absolute;
		right: 16px;
		top: 50%;
		transform: translateY(-50%);
		@include media-breakpoint-down(sm) {
			position: relative;
			right: auto;
			top: auto;
			transform: none;
		}
	}
}