/* Breadcrumb */
.page-title-box{
    padding: 30px 0;
    @include flex-center-y;
    @include n-flex-between;
    @include media-breakpoint-down(sm) {
    	display: block;
    }
}
.page-title{
    font-size: 40px;
    font-weight: 200;
}
.breadcrumb{
	padding: 0;
	margin-bottom: 0;
	background-color: transparent;
}
.breadcrumb-item{
    text-transform: uppercase;
	a{
		color: $muted2;
	}
	&.active{
		color: $muted2;
	}
}

.breadcrumb-item+.breadcrumb-item::before{
    content: ' ';
    display: -webkit-inline-box;
    padding-right: .0;
    margin-right: .5rem;
    border-right: 1px solid $muted2;
    height: 10px;
}