.product-wrapper{
	.product-item{
		padding: 20px;
		position: relative;
		background-color: $white;
		@include box-shadow-hover;
		@include transition(box-shadow .3s ease);
		margin-bottom: 30px;
		.product-title{
			padding: 20px;
			text-align: center;
			font-size: 20px;
			text-transform: uppercase;
		}
		.product-backdrop{
			background-color: rgba($black, .70);
			opacity: 0;
			visibility: hidden;
			@include transition(opacity .3s ease, visibility .3s ease);
			@include position-full;
			@include flex-center;
		}
		.card-ratio-1-1{
			>img{
				-o-object-fit: none;
				object-fit: none;
			}
		}
		&:hover{
			.product-backdrop{
				opacity: 1;
				visibility: visible;
			}
		}
	}
}